import Vue from 'vue'
import axios from 'axios'
import router from './router'
import store from './store/index.js'
import type from './store/type.js'
import { MessageBox } from 'mint-ui';



// axios 
axios.defaults.baseURL = window.api_root ? window.api_root : 'http://localhost:8080/api/';  //
axios.defaults.timeout = 5000;   //
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.params = {apikey:'0b2bdeda43b5688921839c8ecb20399b',count:15};

// axios.request 
axios.interceptors.request.use(
    config => {
        store.dispatch('Loading',true);
        console.log("");
        // if(config.data == undefined){
        //     store.dispatch('Loading',false);
        //     MessageBox('','');
        // }
        // if(store.state.token) {
        //     config.headers.Authorization = `token ${store.state.token}`;
        // }
        // config.headers.Authorization = 'token'
        return config;
    },
    err => {
        console.log(err);
        return Promise.reject(err);
});

// axios.request 
axios.interceptors.response.use(
    response => {
        store.dispatch('Loading',false);
        console.log("");
        return response;
    },
    error => {
        if (error.response) {
            console.log(error)
            switch (error.response.status) {
                case error.response.status:
                    // 401 token
                    store.dispatch('USER_LOGOUT');
                    router.replace({
                        path: 'login',
                        query: {redirect: router.currentRoute.fullPath}
                    });
                    MessageBox('',''+error.response.status);
            }
        }
        // console.log(JSON.stringify(error));//console : Error: Request failed with status code 402
        return Promise.reject(error.response)
});

Vue.prototype.$ajax = axios;



