<template>
    <div class="login" id="login">
        <div class="portrait">
            <img src="./../assets/kedaya.png" height="140" width="140" alt="" />
        </div>
        <mt-field label="" placeholder="" v-model="userName"></mt-field>
        <!-- <mt-field label="" placeholder="" type="password" v-model="userPassword"></mt-field> -->
        <mt-button type="primary" size="large" class="loginBtn" @click="loginBtn"></mt-button>
    </div>
</template>
<script>
    import { mapActions } from 'vuex'
    import { MessageBox } from 'mint-ui';
    export default{
        name:'login',
        data(){
            return {
                userName:'',
                userPassword:'',
            }
        },
        methods:{
            ...mapActions([
              'USER_LOGIN',    //  this.USER_LOGIN()  this.$store.dispatch('USER_LOGIN')
            ]),
            loginBtn(){
                if(this.userName == ''){
                    MessageBox('','');
                    return false;
                }
                let param = {
                    userName:this.userName,
                    token:Math.random().toString(36).substr(2)
                };
                this.USER_LOGIN(param);
                sessionStorage.setItem('user',param);
                // decodeURIComponent   URL
                let redirect = decodeURIComponent(this.$route.query.redirect || '/');
                this.$router.replace({path: redirect});
            }
        }
    }
</script>
<style type="text/css">
    .login{
        margin: 0 0.625rem;
    }
    .portrait{
        text-align: center;
        padding:1.875rem 0;
    }
    .loginBtn {
        margin-top: 0.9375rem;
    }
</style>



